<template>
	<view class="content" @touchstart="start" @touchend="end">
		<view class="view_Career">
			<uni-card class="cardOne" title="学习等级-待删除">
				{{gradeList[User_Grade].gradeName}}
				{{gradeList[User_Grade].gradeNumber}}
			</uni-card>

			<uni-card class="cardTwo" title="">
				<view class="cardTwo_ZXT" v-for="item in daysList">
					<text v-if="item.count===3">⚪</text>
					<text v-else="">·</text>
				</view>


				<br>
				<view class="cardTwo_ZXT" v-for="item in daysList">
					<text v-if="item.count===2">⚪</text>
					<text v-else="">·</text>
				</view>

				<br>


				<view class="cardTwo_ZXT" style="" v-for="item in daysList">
					<text v-if="item.count===1">⚪</text>
					<text v-else="">·</text>
				</view>

				<br>

				<view class="cardTwo_ZXT" v-for="item in daysList">
					<text v-if="item.count===0">⚪</text>
					<text v-else="">·</text>
				</view>

				<br>

				<view class="cardTwo_ZXT" v-for="item in daysList">
					<text>{{item.type}}</text>
				</view>

			</uni-card>
		</view>

		<view class="view_History">
			<uni-card>
				<uni-collapse>
					<uni-collapse-item :open="true" title="今日自习预约">
						<!-- 无预约数据时显示 -->
						<uni-list-item v-if="!test()[1]" title="无预约" note="书山有路勤为径,学海无涯苦作舟!" rightText="">
						</uni-list-item>
						<!-- 有预约数据时显示 -->
						<uni-list-item v-if="item.UserRoom_Date==test()[0]" v-for="(item,index) in UserRoomList"
							:title="item.UserRoom_Room" :note="item.UserRoom_Room"
							:rightText="item.UserRoom_Date+'-'+item.UserRoom_Time">
						</uni-list-item>
					</uni-collapse-item>
				</uni-collapse>
			</uni-card>

			<uni-card>
				<uni-collapse>
					<uni-collapse-item :open="false" title="历史学习记录">
						<uni-list-item v-if="item.UserRoom_Date<test()[0]" v-for="(item,index) in UserRoomList"
							:title="item.UserRoom_Room" :note="item.UserRoom_Date" :rightText="item.UserRoom_Time">
						</uni-list-item>
					</uni-collapse-item>
				</uni-collapse>
			</uni-card>
		</view>

		<view class="view_Test">
			<uni-card> <text>---其他功能占位---</text></uni-card>
		</view>





		<view>
			<!-- 提示信息弹窗 -->
			<uni-popup ref="message" type="message">
				<uni-popup-message :type="msgType" :message="messageText" :duration="2000"></uni-popup-message>
			</uni-popup>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				msgType: 'wran',
				messageText: '这是一条成功提示',

				gradeList: [{
					gradeName: "Lv.小白",
					gradeNumber: 100
				}, {
					gradeName: "Lv.伴读",
					gradeNumber: 200
				}, {
					gradeName: "Lv.童生",
					gradeNumber: 400
				}, {
					gradeName: "Lv.秀才",
					gradeNumber: 800
				}, {
					gradeName: "Lv.举人",
					gradeNumber: 1600
				}, {
					gradeName: "Lv.进士",
					gradeNumber: 3200
				}, {
					gradeName: "Lv.状元",
					gradeNumber: 99999999
				}],

				User_Grade: 0,

				UserRoomList: [],

				daysList: [],

				startData: {
					clientX: 0,
					clientY: 0
				}
			};
		},
		onShow: function() {
			console.log("MAIN-SHOW");
			const _that = this
			uni.getStorage({
				key: 'daysList',
				success: function(res) {
					if (res.data.length > 0) {
						_that.daysList = res.data
					} else {
						_that.daysList = []
					}
				}
			})
			// 获取status状态,判断是否已经登录
			uni.getStorage({
				key: 'hasLogin',
				success: function(res) {
					if (res.data) {
						uni.getStorage({
							key: 'UserInfo',
							success: function(res) {
								if (_that.UserRoomList.length) {
									if (_that.UserRoomList[0].UserRoom_Account === res.data
										.UserInfo_Account) {
										console.log("本地已存储UserRoomList数据！");
									} else {
										console.log("账号不一致，发起UserRoomList数据请求！");
										_that.getUserRoom(res.data.UserInfo_Account)

									}
								} else {
									console.log("本地无数据，发起UserRoomList数据请求！");
									_that.getUserRoom(res.data.UserInfo_Account)
								}
							}
						});
					} else {
						console.log("您未登陆或未完善个人信息，无法获取预约信息，请登录或前往完善！");
						_that.UserRoomList = []
						_that.messageToggle('warn')
					}
				}
			});
		},
		methods: {

			start: function(e) {
				this.startData.clientX = e.changedTouches[0].clientX
				this.startData.clientY = e.changedTouches[0].clientY
				console.log(this.startData);
			},

			end: function(e) {

			},

			test: function() {
				let nowdate = new Date();
				let string = nowdate.getFullYear() + "-" + (nowdate.getMonth() + 1) + "-" + nowdate.getDate()

				let count = 0
				for (let i in this.UserRoomList) {
					if (this.UserRoomList[i].UserRoom_Date == string) {
						count++
					}
				}
				return [string, count]
			},

			messageToggle(type) {
				this.msgType = type
				this.messageText = `您未登陆，无法获取预约信息，请登录！`
				this.$refs.message.open()
			},

			getUserRoom: function(e) {
				const _that = this
				uni.request({
					url: "http://127.0.0.1:3007/api/getUserRoom",
					method: "POST",
					data: 'UserRoom_Account=' + e,
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					timeout: 6000,
					success: (res) => {
						const status = res.data.status
						if (status === 200) {
							// console.log("服务器返回", res.data.data);
							_that.UserRoomList = res.data.data
							console.log('setUserRoomList-YES');
							_that.xxqs(res.data.data)
							// console.log("本地的数据", _that.UserRoomList);
						}
					},
					fail: (err) => {
						console.log(err, "err");
					}
				})
			},

			xxqs: function(list) {
				const _that = this
				let timenow = new Date().getTime(),
					timeold,
					day
				let daysList = [{
					type: "",
					count: 0
				}, {
					type: "",
					count: 0
				}, {
					type: "",
					count: 0
				}, {
					type: "",
					count: 0
				}, {
					type: "",
					count: 0
				}, {
					type: "",
					count: 0
				}, {
					type: "",
					count: 0
				}]
				for (let item in list) {
					timeold = new Date(list[item].UserRoom_Date);
					day = (Math.abs(timenow - timeold.getTime()) / (1000 * 24 * 60 * 60)).toFixed(0)
					// console.log(timeold.getDate());
					// console.log(daysList[day].type);
					// daysList[day].type = timeold.getDate()
					daysList[day].count++
				}
				for (let item in daysList) {
					daysList[item].type = Number(new Date().getDate()) - Number(item)
					// console.log(daysList[item].type,daysList[item].count);
				}
				// this.daysList = daysList.reverse();
				// this.daysList = daysList;
				uni.setStorage({
					key: 'daysList',
					data: daysList,
					success: function() {
						_that.daysList = daysList
						console.log('setdaysList-YES');
					}
				});
				// console.log(this.daysList);
			}
		},
	}
</script>

<style lang="less">
	.content {
		font-size: 36rpx;


		.view_Career {
			.cardOne {

				.showQR_Btn {
					width: 80rpx;
					height: 80rpx;
					line-height: 80rpx;
					text-align: center;
					border-radius: 80rpx;
					background-color: aqua;
				}

				.showQR {
					width: 400rpx;
					height: 400rpx;
					background-color: aqua;
					text-align: center; //待删除
					line-height: 400rpx; //待删除
				}
			}

			.cardTwo {

				// height: 300rpx;
				.cardTwo_ZXT {
					position: relative;
					width: 14.28%;
					float: right;
					text-align: center;
					// background-color: aqua;
				}
			}
		}

		.view_History {
			// background-color: aqua;
		}

		.view_Test {
			text-align: center;
		}

	}
</style>
